@import url("https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,400;0,500;0,600;1,300&display=swap");
* {
  font-family: "Raleway", sans-serif;
}
body {
  padding: 20px;
  background-color: #0f0e17;
  color: #fffffe;
}
h1,
h3,
.gif-title {
  color: #fffffe;
}
input {
  width: 20%;
  font-size: 1.2rem;
  color: grey;
  background: #eeeeee;
  border: none;
  margin-bottom: 20px;
  height: 30px;
  outline: none;
}
input:focus {
  outline: 2px solid#e53170;
}
.gif-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 15px;
}
.gif-item {
  align-self: center;
  width: 100%;
  overflow: hidden;
}
.gif-item img {
  width: 100%;
  border-radius: 5px;
}
.gif-title {
  font-weight: 500;
  text-align: center;
}
.gif-user {
  font-weight: 300;
  color: #a7a9be;
  font-style: italic;
}
.gif-category {
  font-weight: 600;
  font-size: 30px;
  text-transform: capitalize;
}
.btn {
  border: none;
  background-color: #ff8906;
  font-weight: bold;
  color: #fffffe;
  width: 110px;
  padding: 8px;
  font-size: 18px;
  margin-left: 20px;
  border-radius: 50px;
}
.btn:hover {
  color: #ff8906;
  background-color: #fffffe;
  cursor: pointer;
}
/*SPINNER*/
.spinner {
  width: 40px;
  height: 40px;

  position: relative;
  margin: 100px auto;
}

.double-bounce1,
.double-bounce2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #e53170;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;

  -webkit-animation: sk-bounce 2s infinite ease-in-out;
  animation: sk-bounce 2s infinite ease-in-out;
}

.double-bounce2 {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}

@-webkit-keyframes sk-bounce {
  0%,
  100% {
    -webkit-transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
  }
}

@keyframes sk-bounce {
  0%,
  100% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }
  50% {
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}
